


























import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from 'vant';
import comhead from "../../components/Comhead.vue";

// import { recommendList } from '../../service/getData';

@Component({
  components: {
    comhead
  },
  mixins: [mixin]
}) 
export default class forget extends Vue {
         @Action("setLoginVerifyCode") setLoginVerifyCode;
        loginName=""
        password=""
        repassword=""
        smsCode=""
        isGetverify =true//当前允许发送验证码
        timerNum=60
        timer=60
        timelop(){
            let self = this;

            console.log('获取验证码')
            self.timer = setInterval(function(){
                    self.isGetverify =true
                if(self.timerNum >=1){
                    self.timerNum--;
                }else{
                    clearInterval(self.timer);
                    self.timerNum = 60;
                }
                  // let verInfo={
                  //       type : 'setsign',
                  //       num : self.timerNum
                  //   }
                self.setLoginVerifyCode(self.timerNum);                
           
                console.log(self.timerNum)
            },1000)
            
        }
    
  getVistyCode() {
//验证手机号码
   if(this.loginName ==''){
     Toast('请输入手机号码');
     return
   }
            if (!this.isGetverify) {
              return 
            }
            
          Vue.prototype.$reqFormPost("/auth/getsmscode",{mobile:this.loginName	,type:'FINDPASSWORD'},res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
Toast(res.data.message)

          return;
        }
        Toast("发送成功")
            this.timelop();
      })


        }
        vistyText() {

            if (this.$store.getters[Vue.prototype.MutationTreeType.VERCODE] < 60) {
                this.isGetverify = false
                return this.$store.getters[Vue.prototype.MutationTreeType.VERCODE] + 's'
            } else {
                this.isGetverify = true
                return '获取验证码'
            }
        }
 doforget(){
    //验证
   
   if(this.loginName ==''){
     Toast('请输入手机号码');
     return
   }
   if(this.smsCode ==''){
     Toast('请输入验证码')
     return
   }

   if(this.repassword==''){
     Toast('请输入确认密码')
     return
   }

   if(this.password==''){
     Toast('请输入新密码');
     return 
   }
    if (this.password.length<6|| this.repassword.length<6) {
        Toast('最低字符为6位')
      return;
    }
     if(this.password != this.repassword){
      Toast('两次输入密码不一致 ')
      return
    }
    Vue.prototype.$reqFormPost("/user/password/find",{
       mobile:this.loginName,
       password:require('crypto').createHash('md5').update(this.password).digest('hex'),
       smsCode:this.smsCode,
       },res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
         
            Toast(res.data.message)

          return;
        }
        Toast("保存成功")
        this.$router.push("/login")
      })
 }  
 handlePX(CssName, PxNumber) {
    return CssName +":" +this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /750 * PxNumber +"px;";
  }
  mounted() {


    if(this.$store.getters[Vue.prototype.MutationTreeType.VERCODE] < 60){
            this.timerNum = this.$store.getters[Vue.prototype.MutationTreeType.VERCODE];
            // this.isGetverify= true;
            this.timelop();
            }
  }
}



